<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>作者: {{blog.author}}</p>
		<p>分类:</p>
		<ul>
			<li v-for="category in blog.categories" :key="category">
				{{category}}
			</li>
		</ul>
    <button @click="deleteBlog()">删除</button>
    <router-link :to="'/edit/' + id">编辑</router-link>
	</div>
</template>

<script>
// import axios from 'axios'
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get('https://wd7520381365bfdcjz.wilddogio.com/posts/' + this.id + ".json")
			// axios.get('/posts/' + this.id + ".json")
				.then((data) => {
          console.log(data)
					this.blog = data.body;
				})
    },
    methods: {
      deleteBlog() {
        this.$http.delete('https://wd7520381365bfdcjz.wilddogio.com/posts/' + this.id + '.json')
        .then(response =>{
										this.$router.push({path:'/'})
									})
      }
    }
	}
</script>
<style scoped>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #aaa;
    text-align: left;
	}
</style>


