<template>
  <div v-theme:column="'narrow'" id="show-blogs">
      <h1>博客总览</h1>
      <input type="text" v-model="search" placeholder="搜索博客"> 
      <div v-for="blog in filterBlogs" class="single-blog" :key="blog.title">
      <router-link :to="'/blog/'+blog.id">
        <h2 v-rainbow>
          {{blog.title | toUpperCase}}
        </h2>
      </router-link>
        <article>
          {{blog.content | snippet}}
        </article>
      </div>
     
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    this.$http
      .get("https://wd7520381365bfdcjz.wilddogio.com/posts.json")
      .then(function(data){
        console.log(data.body)
        return data.body
  			// console.log(data.json());
  			// this.blogs = data.body.slice(0,10);
  			// console.log(this.blogs);
  		})
      .then(function(data){
        var blogsArray = [];
        for(let key in data){
          // console.log(key);
          // console.log(data[key]);
          data[key].id = key;
          blogsArray.push(data[key]);
        }
        // console.log(blogsArray);
        this.blogs = blogsArray;
        console.log(this.blogs);
      })
  },
  computed: {
    filterBlogs: function() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  },
  filters: {
    // 'to-uppercase': function (val) {
    //   return val.toUpperCase();
    // },
    toUpperCase(val) {
      return val.toUpperCase();
    }
  },
  directives: {
    'rainbow': {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      },
      
    },
    'theme': {
        bind(el, binding, vnode) {
          if (binding.value == "wide") {
            el.style.maxWidth = "1260px";
          } else if (binding.value == "narrow") {
            el.style.maxWidth = "560px";
          }
          if (binding.arg == "column") {
            el.style.background = "#6677cc";
            el.style.padding = "20px";
          }
        }
      }
  }
};
</script>

<style scoped>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background-color: #eee;
}
#show-blogs a{
  color: #444;
  text-decoration: none;
}
input[type="text"]{
  padding: 8px;
  width: 100%;
  border-radius: 10px;
  outline: none;
  border: none;
  box-sizing: border-box;
  transition: all .2s ease-in
}
input[type="text"]:focus{
  box-shadow: 0px 0px 6px #0f0
}
</style>



