<template>
  <div id="add-blog">
      <h2>添加博客</h2>
      <form action="" v-if="!submmited">
          <label for="">博客标题</label>
          <input type="text" v-model="blog.title" required/>
          <label for="">博客内容</label>
          <textarea name="" id="" cols="30" rows="10" v-model="blog.content"></textarea>
          <div id="checkboxes">
              <label for="">Vue.js</label>
              <input type="checkbox" value="Vue.js" v-model="blog.categories">
              <label for="">React.js</label>
              <input type="checkbox" value="React.js" v-model="blog.categories">
              <label for="">Node.js</label>
              <input type="checkbox" value="Node.js" v-model="blog.categories">
              <label for="">Angular.js</label>
              <input type="checkbox" value="Angular.js" v-model="blog.categories">
          </div>
          <label for="">作者</label>
          <select name="" id="" v-model="blog.author">
               <option v-for="author in authors" :key="author">
                   {{author}}
               </option>
          </select>
          <button @click.prevent="post">添加博客</button>
      </form>
      <h3 v-if="submmited"> 您的博客发布成功</h3>
      <div id="preview">
          <h3>博客总览</h3>
          <p>博客标题：{{blog.title}}</p>
          <p>博客内容：</p>
          <p>{{blog.content}}</p>
          <p>博客分类</p>
          <ul>
              <li v-for="category in blog.categories" :key="category">
                  {{category}}
              </li>
          </ul>
          <p>作者：{{blog.author}}</p>
      </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog: {
          title: '',
          content: '',
          categories: [],
          author: ''
      },
      submmited: false,
      authors: ['eason', 'jay', 'superman']
    }
  },
  methods: {
      post: function () {
          this.$http.post('https://wd7520381365bfdcjz.wilddogio.com/posts.json',this.blog).then((data) => {
              console.log(data)
              this.submmited = true;
          })
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #add-blog *{
        box-sizing: border-box;
    }
    #add-blog{
        margin: 20px auto;
        max-width: 600px;
        padding: 20px;
    }
    select{
        border-radius: 10px;
        outline: none;
    }
    label{
        display: block;
        margin: 20px 0 10px;
    }
    input[type="text"], textarea, select{
        display: block;
        width: 100%;
        padding: 8px;
    }
    textarea{
        height: 200px;
    }
    #checkboxes label{
        display: inline-block;
    }
    #checkboxes input{
        display: inline-block;
        margin-right: 10px;
    }
    button{
        display: block;
        margin: 20px 0;
        background: crimson;
        color: #fff;
        padding: 14px;
        border-radius: 14px;
        font-size: 18px;
        cursor: pointer;
    }
    #preview{
        padding: 10px 20px;
        border:1px dotted #ccc;
        margin: 30px 0;
    }
    h3{
        margin-top: 10px;
    }
</style>



